//       watched
//   id   id   ( API)
//    -  true,   - false

export function isWatched(userId, filmId) {
  let result = false;
  const dbRef = ref(db, `user/${userId}/watched/`);
  onValue(
    dbRef,
    snapshot => {
      snapshot.forEach(childSnapshot => {
        if (childSnapshot.val().id === filmId) {
          result = true;
          return result;
        }
      });
    },
    {
      onlyOnce: true,
    }
  );
  return result;
}

